import React from 'react'
import { Card, Avatar,Badge } from "antd";

import {useHistory} from "react-router-dom";
export const PostCard = ({id,img,funds,title}) => {
      
    const { Meta } = Card;
    const history=useHistory();
    return (
      <div style={{ width: "98%", margin: "10px" }}>
        <Badge.Ribbon
          text={funds ? "Sponsered" : "Need Funds"}
          color={funds ? "green" : "red"}
        >
          <Card
          
            onClick={() => {
              history.push(`/post/${id}`);
            }}
            hoverable
            style={{ width:"100%"}}
          >
            <div style={{display:"flex"}}>
            <img alt="Nature enviorment" src={img} height="300px" width="500px"/>
            <div style={{display:"flex",justifyContent:"center",alignItems:"center",marginLeft:"20px"}}>
            <Meta
              avatar={
                <Avatar src="https://lh3.googleusercontent.com/proxy/SIUjh18071tdHH_4Ge_eboRJDDSP6bEmlv8HiNBqCJgCvtFHg9jl7aQ835QR49y5iJULadJn2Bqlsnqg4z4mPIGRK6O0SeXbpPMKO62u1gKDa78SQgYGD6DdSoWWeIs" />
              }
              title={title}
            />
            </div>
            </div>
          </Card>
        </Badge.Ribbon>
      </div>
    );
}
