import React from 'react'
import { Form, Input, Button,  Card } from "antd";
const Funds = () => {
    const onFinish = (values) => {
      console.log("Success:", values);
    };

    const onFinishFailed = (errorInfo) => {
      console.log("Failed:", errorInfo);
    };
    return (
      <div
        style={{
          height: "100vh",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <h2>Payment</h2>
        <Card hoverable style={{ width: "60vh" }}>
          <Form
            name="basic"
            labelCol={{}}
            wrapperCol={{}}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
          >
            <Form.Item
              name="total"
              rules={[
                {
                  required: true,
                  message: "Please enter total Amount!",
                },
              ]}
            >
              <Input placeholder="Total Amount in $" />
            </Form.Item>

            <Form.Item
              name="1"
              rules={[
                {
                  required: true,
                  message: "Please Enter first Installemnt!",
                },
              ]}
            >
              <Input placeholder="first Installemnt Amount in $" />
            </Form.Item>
            <Form.Item
              name="2"
              rules={[
                {
                  required: true,
                  message: "Please Enter Secoand Installemnt!",
                },
              ]}
            >
              <Input placeholder="Secoand Installemnt Amount in $" />
            </Form.Item>
            <Form.Item
              name="3"
              rules={[
                {
                  required: true,
                  message: "Please Enter Third Installemnt!",
                },
              ]}
            >
              <Input placeholder="Third Installemnt Amount in $" />
            </Form.Item>

            <Form.Item
              wrapperCol={{
                offset: 8,
                span: 16,
              }}
            >
              <Button type="primary" htmlType="submit">
                Set Funds
              </Button>
            </Form.Item>
          </Form>
        </Card>
      </div>
    );
}

export default Funds
